/* -----------------------------------------------------------------------------
  PART: RIGHT SIDEBAR

  Style for the left sidebar, which contains the local TOC.
----------------------------------------------------------------------------- */

@import "../components/mode-switch";
@import "../components/local-toc";

#right-sidebar {
  position: fixed;
  top: $headerHeight;
  right: 0;
  flex-basis: $rightSidebarWidth;
  flex-shrink: 0;
  flex-grow: 0;
  width: $rightSidebarWidth;
  height: calc(100vh - #{$headerHeight} - #{$footerHeight});

  .sidebar-container {
    position: fixed;
    top: $headerHeight;
    right: 0;
    bottom: $footerHeight;
    width: calc((#{$leftSidebarWidth} / 2) + 30px);
    padding: 30px 16px;
    margin-left: auto;
    overflow: hidden;
    background-color: $wuiColorEmptyShade;

    .edit-repo {
      width: 100%;
      height: 40px;
      padding: 8px  0;
      border-top: 1px solid $wuiColorNeutralLight;

      a {
        color: $wuiColorDarkestShade;
        color: $wuiColorPrimary;

        &:hover,
        &:focus {
          color: $wuiColorPrimaryLighter;
        }

        .icon {
          margin-left: 10px;
          font-size: 14px;
          color: $wuiColorDarkestShade;
        }
      }
    }
  }
}
