/* -----------------------------------------------------------------------------
  Search Results page
----------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Variables (inc)
----------------------------------------------------------------------------- */
/* -- Device screens variables ---------------------------------------------------------------------------- */
/* -- Color variables ------------------------------------------------------------------------------------ */
/* -- Theme elements fixed dimensions ------------------------------------------------------------------------------------ */
/* -- Others ------------------------------------------------------------------------------------ */
/* -----------------------------------------------------------------------------
  Mixins & functions (inc)
----------------------------------------------------------------------------- */
