/* -----------------------------------------------------------------------------
  Root (inc)
----------------------------------------------------------------------------- */
body {
  position: relative;
  margin-top: 77px;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-weight: 500;
}

#icon-sprite {
  display: none;
}

.inner-body {
  position: relative;
  width: 100%;
}

main {
  padding-left: 30px;
  padding-right: 15px;
  min-height: calc(100vh - 77px - 60px - 70px);
}

main img {
  max-width: 100%;
  margin-bottom: 1rem;
}

main p {
  font-family: Manrope, sans-serif;
  font-style: normal;
  letter-spacing: 0.03em;
}

main table {
  margin-bottom: 1rem;
}

main table td,
main table th {
  vertical-align: baseline;
}

.sidebar {
  flex-shrink: 0;
}

.sidebar .sidebar-container {
  top: 0;
  overflow-y: auto;
}

a {
  color: #256bd1;
}

a:hover, a:focus {
  color: #3585f9;
}

a:active {
  color: #256bd1;
  font-weight: 700;
}

th p {
  margin: 0;
  font-weight: inherit;
  font-weight: 500;
}

/* -----------------------------------------------------------------------------
  Defaults for content (inc)

  General complete style specifically required for the content, including
  lists, tables, etc. that is not necessarily need in all pages.
----------------------------------------------------------------------------- */
/* Fixes the anchor */
.section {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.section::before {
  content: '';
  display: block;
  position: relative;
  width: 0;
  height: 1rem;
  margin-top: -1rem;
}

/* -----------------------------------------------------------------------------
  PART: HEADER

  Style for the header/top bar of the page.
----------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Component: Brand and logo

  Style for the element containing the logo in the header.
----------------------------------------------------------------------------- */
.navbar-brand {
  display: flex;
  justify-content: flex-end;
}

.navbar-brand .logo-link.web {
  padding-bottom: .3rem;
}

.navbar-brand .logo-link.docs {
  margin-left: 5px;
  line-height: 2.15;
  color: #292929;
}

.navbar-brand .logo-link.docs:hover {
  text-decoration: none;
}

/* -----------------------------------------------------------------------------
  Component: Navigation bar

  Style for the menu with links that are external to the documentation (the
  menu in the top bar).
----------------------------------------------------------------------------- */
.navbar-nav .nav-link {
  padding: 0 10px;
  font-size: 18px;
  color: #292929;
}

.navbar-nav .nav-link:hover, .navbar-nav .nav-link:focus {
  fill: #256bd1;
  color: #256bd1;
  text-decoration: none;
}

.navbar-nav .nav-link:active {
  font-weight: 500;
}

.navbar-nav .nav-link.dropdown-toggle {
  padding-right: 27px;
}

.navbar-nav .nav-link.dropdown-toggle[aria-expanded="true"] {
  text-decoration: none;
}

.navbar-nav .nav-link.dropdown-toggle[aria-expanded="true"]::after {
  transform: rotateX(180deg);
  transition: transform ease-in .1s;
}

.navbar-nav .nav-link.dropdown-toggle::after {
  content: '';
  display: inline-block;
  width: 13px;
  height: 8px;
  background: url("../../images/icons/svg-icons.svg#menu-chevron");
  background-size: contain;
  background-position: center;
  position: absolute;
  right: 8px;
  bottom: .7rem;
  border: none;
  transition: transform ease-in .1s;
}

.navbar-nav .dropdown-menu {
  min-width: unset;
  padding: 15px;
  margin-top: 20px;
  border: 0;
  border-radius: 10px;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.05);
}

.navbar-nav .dropdown-menu::before {
  position: absolute;
  top: -9px;
  width: 18px;
  height: 18px;
  content: "";
  background-color: #fff;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.05);
  clip-path: inset(-15px 0 0 -15px);
  transform: rotate(45deg);
}

.navbar-nav .dropdown-menu .dropdown-item {
  padding: .25rem .75rem;
}

/* -----------------------------------------------------------------------------
  Component: Version selector

  Style for the release version selector.
----------------------------------------------------------------------------- */
#version-selector.show .dropdown-toggle::after {
  transform: rotate(180deg);
}

#version-selector .dropdown-btn {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: calc((385px + 32px) / 2);
  height: 40px;
  background-color: #fff;
  border: 2px solid #fedd0b;
  border-radius: 10px;
}

#version-selector .dropdown-btn[aria-expanded="false"]:focus {
  box-shadow: inset 0 0 2px 2px rgba(101, 124, 151, 0.2);
}

#version-selector .dropdown-btn:focus {
  box-shadow: none;
}

#version-selector .dropdown-menu {
  top: 52px;
  box-sizing: border-box;
  width: 100%;
  border: 2px solid #fedd0b;
  border-radius: 10px;
}

#version-selector .dropdown-menu > li > a {
  display: inline-block;
  width: 100%;
  padding: .16rem .75rem;
  color: #292929;
  text-decoration: none;
}

#version-selector .dropdown-menu > li > a:hover, #version-selector .dropdown-menu > li > a:focus {
  font-weight: 700;
  background-color: #ebf2fb;
}

header {
  height: 77px;
  background: #fff;
  box-shadow: 0 4px 12px 0 rgba(53, 133, 249, 0.15);
}

header .navbar {
  height: 77px;
}

/* -----------------------------------------------------------------------------
  PART: LEFT SIDEBAR

  Style for the left sidebar, which contains the global TOC.
----------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Component: Search box

  Style for the searchbox in general.
----------------------------------------------------------------------------- */
.search-box {
  background-color: #ebf2fb;
  border: 2px solid #c1ccd9;
  border-radius: 10px;
}

.search-box .search-form {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 46px;
}

.search-box .search-form .form-control {
  position: relative;
  z-index: 1;
  width: 100%;
  font-size: 18px;
  font-weight: 400;
  background-color: #ebf2fb;
  border: none;
}

.search-box .search-form .btn-search {
  position: absolute;
  right: 0;
  z-index: 10;
  width: 46px;
  height: 46px;
  padding: 5px;
  background: url("../../images/icons/svg-icons.svg#search");
  background-size: 41px 41px;
  background-position: center;
  border: none;
}

/* -----------------------------------------------------------------------------
  Component: Global TOC

  Style for the menu containing the full index of the documentation release
  folder.
  Note: the elements of the global TOC are automatically generated by the html
  builder.
----------------------------------------------------------------------------- */
#global-toc ul {
  padding: 0;
  list-style: none;
}

#global-toc ul .current-toc-node {
  color: #256bd1;
}

#global-toc ul .current-toc-node:hover {
  color: #3585f9;
}

#global-toc ul .current-toc-node::before {
  position: absolute;
  top: 1.1rem;
  display: inline-block;
  width: 6px;
  height: 6px;
  pointer-events: none;
  content: "";
  border: 5px solid #256bd1;
  border-right-color: transparent;
  border-bottom: 0;
  border-left-color: transparent;
  transform: rotate(-90deg);
}

#global-toc .show > ul {
  display: block;
}

#global-toc li > ul {
  display: none;
}

#global-toc a {
  position: relative;
  display: inline-block;
  width: 100%;
  padding: 5px 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 32px;
  color: #292929;
  text-decoration: none;
}

#global-toc a:hover {
  color: #3585f9;
}

#global-toc .current .toctree-l2 > a {
  padding-left: 20px;
}

#global-toc .current .toctree-l2 > a.current-toc-node::before {
  left: 5px;
}

#global-toc .current .toctree-l3 > a {
  padding-left: 40px;
}

#global-toc .current .toctree-l3 > a.current-toc-node::before {
  left: 25px;
}

#global-toc .current .toctree-l4 > a {
  padding-left: 60px;
}

#global-toc .current .toctree-l4 > a.current-toc-node::before {
  left: 45px;
}

#global-toc .current .toctree-l5 > a {
  padding-left: 80px;
}

#global-toc .current .toctree-l5 > a.current-toc-node::before {
  left: 65px;
}

#global-toc .current .toctree-l6 > a {
  padding-left: 100px;
}

#global-toc .current .toctree-l6 > a.current-toc-node::before {
  left: 85px;
}

#global-toc [class*="toctree-l"].current > a {
  font-weight: 700;
}

#global-toc [class*="toctree-l"].current.show > ul {
  display: block;
}

#global-toc [class*="toctree-l"] > ul {
  display: none;
}

#global-toc .toctree-l1 > a {
  padding-left: 40px;
}

#global-toc .toctree-l1 > a::before {
  left: 25px;
}

#global-toc .toctree-l1.show > a {
  background-color: #dcebff;
}

#global-toc .toctree-l1 > ul {
  margin-left: 40px;
  border-left: 1px solid #256bd1;
}

#left-sidebar {
  position: fixed;
  top: 77px;
  flex-basis: 385px;
  flex-shrink: 0;
  flex-grow: 0;
  width: 385px;
}

@media (min-width: 992px) {
  #left-sidebar {
    display: block;
  }
}

#left-sidebar .sidebar-container {
  width: 385px;
  height: calc(100vh - 77px);
  padding: 30px 0;
  background-color: #ebf2fb;
}

#left-sidebar .sidebar-container > nav {
  margin-top: 20px;
}

#left-sidebar .sidebar-container .search-box {
  margin-right: 40px;
  margin-left: 40px;
}

/* -----------------------------------------------------------------------------
  PART: RIGHT SIDEBAR

  Style for the left sidebar, which contains the local TOC.
----------------------------------------------------------------------------- */
/* -----------------------------------------------------------------------------
  Component: Mode switch

  Style for the component that switches the theme style to light or dark mmode.
----------------------------------------------------------------------------- */
#light-dark-switch {
  display: flex;
  flex-direction: row;
  padding-bottom: 8px;
}

#light-dark-switch > span:first-child {
  margin-left: 8px;
}

#light-dark-switch .switch-icon {
  width: 20px;
  height: 20px;
  margin-left: 5px;
  margin-right: 5px;
  display: inline-block;
  background-size: contain;
  background-position: center;
}

#light-dark-switch .switch-icon.icon-sun {
  background: url("../../images/icons/svg-icons.svg#sun");
}

#light-dark-switch .switch-icon.icon-moon {
  background: url("../../images/icons/svg-icons.svg#moon");
}

#mode-switch {
  box-sizing: content-box;
  position: relative;
  top: 1px;
  display: flex;
  flex-direction: row;
  width: 26px;
  height: 16px;
  padding: 0;
  margin: 0 5px;
  background-color: #3585f9;
  border: 1px solid #3585f9;
  border-radius: 25px;
}

#mode-switch.dark {
  justify-content: flex-end;
}

#mode-switch.dark span {
  left: calc(100% - 16px);
}

#mode-switch span {
  position: absolute;
  left: 0;
  display: inline-block;
  width: 16px;
  height: 16px;
  content: "";
  background-color: #fff;
  border-radius: 50%;
  transition: left ease-in .2s;
}

/* -----------------------------------------------------------------------------
  Component: Local TOC

  Style for the menu containing the index of the current page.
  Note: the elements of the local TOC are automatically generated by the html
  builder.
----------------------------------------------------------------------------- */
#local-toc {
  padding: 0;
}

#local-toc > ul > li > ul > li > a {
  padding-left: 20px;
}

#local-toc > ul > li > ul > li > ul > li > a {
  padding-left: 30px;
}

#local-toc > ul > li > ul > li > ul > li > ul > li > a {
  padding-left: 40px;
}

#local-toc > ul > li > ul > li > ul > li > ul > li > ul > li > a {
  padding-left: 50px;
}

#local-toc > ul > li > ul > li > ul > li > ul > li > ul > li > ul > li > a {
  padding-left: 60px;
}

#local-toc > ul > li > ul > li > ul > li > ul > li > ul > li > ul > li > ul > li > a {
  padding-left: 70px;
}

#local-toc .toc-title {
  display: inline-block;
  margin-top: 20px;
  height: 30px;
  font-size: 18px;
  font-weight: 700;
  color: #292929;
  text-transform: uppercase;
}

#local-toc > ul {
  height: calc(100vh - 77px - 60px - 40px - 30px - 30px - 70px);
  overflow-y: auto;
}

#local-toc ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

#local-toc ul a {
  font-size: 14px;
  border-left: 2px solid #fff;
  line-height: 24px;
  padding-top: 4px;
  padding-bottom: 4px;
}

#local-toc ul a:hover, #local-toc ul a:focus {
  color: #3585f9;
  outline: none;
}

#local-toc ul a:active {
  font-weight: 700;
  color: #256bd1;
}

#local-toc ul .active {
  font-weight: 700;
  color: #256bd1;
  border-left-color: #256bd1;
}

#right-sidebar {
  position: fixed;
  top: 77px;
  right: 0;
  flex-basis: 290px;
  flex-shrink: 0;
  flex-grow: 0;
  width: 290px;
  height: calc(100vh - 77px - 70px);
}

#right-sidebar .sidebar-container {
  position: fixed;
  top: 77px;
  right: 0;
  bottom: 70px;
  width: calc((385px / 2) + 30px);
  padding: 30px 16px;
  margin-left: auto;
  overflow: hidden;
  background-color: #fff;
}

#right-sidebar .sidebar-container .edit-repo {
  width: 100%;
  height: 40px;
  padding: 8px  0;
  border-top: 1px solid #e9e9e9;
}

#right-sidebar .sidebar-container .edit-repo a {
  color: #292929;
  color: #256bd1;
}

#right-sidebar .sidebar-container .edit-repo a:hover, #right-sidebar .sidebar-container .edit-repo a:focus {
  color: #3585f9;
}

#right-sidebar .sidebar-container .edit-repo a .icon {
  margin-left: 10px;
  font-size: 14px;
  color: #292929;
}

